<template>
	<div class="upPassword">
		<!--修改密码返回-->
		<div class="upPassword-back">
			<div class="left" @click="backGo"><img src="/img/wode/shezhi/shezhi-back.png" alt="" /></div>
			<div class="right">修改密码</div>
		</div>
		<!--修改密码-->
		<div class="upPassword-items">
			<div class="item">
				<div class="item-left">请输入当前登录密码</div>
				<input type="text" placeholder="请输入当前登录密码" />
			</div>
			<div class="item2">
				<div class="item-left">确认新密码</div>
				<input type="text" placeholder="请输入验证码"/>
			</div>
		</div>
		<!--说明-->
		<div class="content">
			<p>必须是6-20个英文字母、数字或符号（除空格），且字母、数字和标点 符号至少包含两种</p>
		</div>
		<!--提交-->
		<div class="upPassword-bottom">
			<div class="tj" @click="tj">提交</div>
		</div>
		<!-- 弹出层-->
		<div class="tanchuceng" v-show="show"></div>
		<div class="tanchu-logo" v-show="show"><img src="/img/shezhi-logo2.png" alt="" /></div>
		<div class="tanchu" v-show="show">
			<div class="tanchu-top">
				<div class="tanchu-title">温馨提示</div>
				<div class="tanchu-content">您确定要修改密码吗？</div>
			</div>
			<div class="tanchu-bottom">
				<div @click="tanChuFalse" class="tanchu-left">确定</div>
				<div @click="tanChuEnd" class="tanchu-right">取消</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "upPassword",
		data: function(){
			return {
				show:false,
				username: ''
			}
		},
		methods:{
			backGo(){
				this.$router.go(-1)
			},
			// 提交事件
			tj(){
				console.log('提交事件')
				this.show = true
				var mo = function(e) {
					e.preventDefault()
				}
				document.body.style.overflow = 'hidden'
				document.addEventListener('touchmove', mo, false)
			},
			// 弹出层 确定
			tanChuFalse(){
				this.show = false
				var mo = function(e) {
					e.preventDefault()
				}
				document.body.style.overflow = ''
				document.addEventListener('touchmove', mo, false)
//				this.$store.commit('clearUser')
				console.log("修改成功")
			},
			// 弹出层 取消
			tanChuEnd(){
				this.show = false
				var mo = function(e) {
					e.preventDefault()
				}
				document.body.style.overflow = ''
				document.addEventListener('touchmove', mo, false)
			}
		}
	}
</script>

